











































































import Vue from "vue";
import { Component, Watch } from "vue-property-decorator";
import Layout from "@/components/Layout.vue";
import TagList from "@/components/Money/TagsList.vue";
import {
  entertainmentTags,
  foodTags,
  houseTags,
  medicalTags,
  shoppingTags,
  transportTags,
} from "@/constants/defaultTags";
import Icon from "@/components/Icon.vue";
import clone from "@/lib/clone";
@Component({
  components: { Icon, TagList, Layout },
})
export default class OutLabel extends Vue {
  tag: TagItem = { name: "food", value: "餐饮" };
  foodTags = foodTags;
  shoppingTags = shoppingTags;
  transportTags = transportTags;
  houseTags = houseTags;
  entertainmentTags = entertainmentTags;
  medicalTags = medicalTags;
  back() {
    this.$router.replace("/money");
  }
  ok() {
    // 触发store 将tag 加入到list 和localS
    this.$store.commit("insertTag", clone(this.tag));
    if (this.$store.state.tagListError == "duplicate") {
      window.alert("标签已经添加过了");
    } else {
      this.$router.replace("/money");
    }
  }

  // @Watch("tag", { deep: true })
  // onTagChange(val: TagItem) {
  //   console.log(val.name);
  //   console.log(val.value);
  // }
}
